.process {
  padding: 120px 0 240px;
  background-color: var( --main-color);
  background-image: url(../img/svg/process/bg.svg);
  background-size: 95%;
  background-repeat: no-repeat;
  background-position: center left;
  &__title {
    opacity: 0;
    visibility: hidden;
    margin-bottom: 120px;
    color: var(--finmodal-title);
    text-align: center;
  }
  &__inners {
    display: flex;
    flex-direction: column;
    gap: 190px;
  }
  &__inner {
    display: flex;
    position: relative;
    opacity: 0;
    visibility: hidden;
    svg {
      position: absolute;
    }
    &--first {
      svg {
        top:0;
        right: 0;
      }
    }
    &--second {
      justify-content: end;
      svg {
        left: -1px;
        bottom: -1px;
      }
    }
  }
  .block-visible {
    opacity: 1;
    visibility: visible;
  }
  &__item {
    padding: 80px 70px;
    max-width: 820px;
    background: #fff;
    box-shadow: 0px 8px 40px rgba(0, 0, 0, 0.2);
    &--text {
      font-weight: 400;
      font-size: 24px;
      line-height: 130%;
      color: var(--title-color);
    }
  }
}
@media screen and (max-width: 768px) {
  .process {
    &__title {
      font-size: 56px;
      text-align: center;
    }   
  }
}
@media screen and (max-width: 425px) {
  .process {
    padding-top: 20px;
    padding-bottom: 100px;
    &__title {
      font-size: 36px;
      text-align: center;
    }
    &__item {
      &--text {
        font-size: 18px;
      }
    }  
  }
}
