.faq {
  padding: 120px 0 240px;
  background-color: #fff;
  &__title {
    margin-bottom: 80px;
  }
  &__item {
    max-width: 880px;
    margin: 0 auto;
    padding: 40px 0;
    position: relative;
    &:not(:last-child) {
      border-bottom: 1px solid #e1e1e1;
    }
    &--title {
      max-width: 634px;
      font-weight: 600;
      font-size: 32px;
      line-height: 140%;
      color: #262626;
      transition: color .4s ease-in-out;
      &__open {
        color: var(--finmodal-title);
      }
    }
    &--btn {
      width: 26px;
      height: 26px;
      background-image: url(../img/svg/faq/plus.svg);
      position: absolute;
      right: 0;
      top: 43px;
      cursor: pointer;
      transform: rotate(0);
      transition: transform .4s ease-in-out;
      &__open {
        transform: rotate(45deg);
      }
    }
    &--content {
      max-width: 700px;
      margin-top: 30px;
      font-weight: 400;
      font-size: 24px;
      line-height: 130%;
      color: #262626;
      transform: scale(1);
      transition: all .4s ease-in-out;
      &.visually-hidden {
        visibility: hidden;
        overflow: hidden;
        opacity: 1;
        clip: rect(0 0 0 0);
        transform: scale(0);
        margin-top: 0;
        font-weight: 0;
        font-size: 0;
        line-height: 0;
      }
    }
  }
}
@media screen and (max-width: 768px) {
  .faq {
    &__title {
      font-size: 48px;
    }
    &__item {
      max-width: 880px;
      margin: 0 auto;
      padding: 40px 10px;
      position: relative;
      &:not(:last-child) {
        border-bottom: 1px solid #e1e1e1;
      }
      &--title {
        max-width: 634px;
        font-weight: 600;
        font-size: 24px;
        line-height: 140%;
        color: #262626;
        transition: color .4s ease-in-out;
        &__open {
          color: var(--finmodal-title);
          font-size: 24px;
        }
      }
      &--btn {
        width: 26px;
        height: 26px;
        background-image: url(../img/svg/faq/plus.svg);
        position: absolute;
        right: -10px;
        top: 43px;
        cursor: pointer;
        transform: rotate(0);
        transition: transform .4s ease-in-out;
        &__open {
          transform: rotate(45deg);
        }
      }
    }
  }
}
@media screen and (max-width: 425px) {
  .faq {
    padding: 80px 0 100px 0;
    &__title {
      font-size: 36px;
    }
    &__item {
      &--title {
        font-size: 18px;
        &__open {
          color: var(--finmodal-title);
          font-size: 18px;
        }
      }
      
    }
  }
}

