.stages {
  background-color: #ffffff;
  padding: 0px 0 120px;
  &__title {
    margin-bottom: 40px;
    height: 104px;
  }
  &__descr {
    margin-bottom: 20px;
    height: 70px;
  }
  &__inner {
    display: flex;
    flex-direction: column;
    
  }
  
  &__item {
    opacity: 0;
    visibility: hidden;
    &--descr {
      max-width: 525px;
      font-weight: 600;
      font-size: 32px;
      line-height: 140%;
      color: var(--title-color);
      text-align: start;
    }
    &--first {
      margin-left:calc(306px - 120px);
    }
    &--second {
      margin-top: -236px;
      margin-left: 700px;
      .stages__item--descr {

        margin-left: 480px;
      }
    }
    &--three {
      margin-top: -390px;
      .stages__item--descr {
        max-width: 640px;
      }
    }
  }
  .block-visible {
    opacity: 1;
    visibility: visible;
  }
}
@media screen and (max-width: 1440px) {
  .stages {
    &__item {
      &--first {
        margin-left: 0;
      }
      &--second {
        margin-top: 0px;
        margin-left: 0;
        .stages__item--descr {
          margin-left: 0px;
        }
      }
      &--three {
        margin-top: 0px;
        .stages__item--descr {
          max-width: 640px;
        }
      }
    }
  }
}
@media screen and (max-width: 936px) {
  .stages {
    &__title {
      margin-bottom: 40px;
      height: 200px;
    }
    &__descr {
      margin-bottom: 20px;
      height: 70px;
    }
  }
}
@media screen and (max-width: 768px) {
  .stages {
    padding-top: 80px;
    &__title {
      font-size: 56px;
      height: 150px;
    }
    &__descr {
      font-size: 24px;
      height: 100px;
    }
    &__item {
      &--descr {
        font-size: 24px;
      }
    }
  }
}
@media screen and (max-width: 425px) {
  .stages {
    &__title {
      font-size: 36px;
    }
    &__item {
      &--descr {
        font-size: 24px;
      }
    }
  }
}
