.futures {
  padding: 120px 0;
  background-color: #262626;
  &__inners {
    display: flex;
    align-items: center;
    flex-direction: column;
    justify-content: space-between;
    gap: 120px;
  }
  &__inner {
    display: flex;
    align-items: center;
    gap: 255px;
    &--title {
      font-weight: 500;
      font-size: 48px;
      line-height: 120%;
      color: #fff;
      margin-bottom: 40px;
    }
  }
  &__text {
    font-weight: 400;
    font-size: 32px;
    line-height: 140%;
    color: #fff;
  }
  ul.futures__text li {
    list-style-type:disc;
    font-weight: 600;
    &:not(:last-child) {
      margin-bottom: 56px;
    }
  }
  &__img {
    max-width: 100%;
  }
}
@media screen and (max-width: 1024px) {
  .futures {
    padding: 120px 0;
    background-color: #262626;
    &__inners {
      display: flex;
      align-items: center;
      flex-direction: column;
      justify-content: space-between;
      flex-wrap: wrap;
      gap: 50px;
    }
    &__inner {
      display: flex;
      justify-content: center;
      gap: 50px;
      align-items: center;
      flex-wrap: wrap;
      &--title {
        font-weight: 500;
        font-size: 48px;
        line-height: 120%;
        color: #fff;
        margin: 0;
      }
    }
    .futures__inner--second {
      flex-wrap: wrap-reverse;
    }
    &__text {
      font-weight: 400;
      font-size: 32px;
      line-height: 140%;
      color: #fff;
    }
    ul.futures__text li {
      list-style-type:disc;
      font-weight: 600;
      &:not(:last-child) {
        margin-bottom: 56px;
      }
    }
    &__img {
      max-width: 100%;
    }
  }
}
@media screen and (max-width: 768px) {
  .futures {
    padding: 60px 0;
    &__inners {
      display: flex;
      align-items: center;
      flex-direction: column;
      justify-content: space-between;
      flex-wrap: wrap;
      gap: 50px;
    }
    &__inner {
      display: flex;
      justify-content: center;
      gap: 50px;
      align-items: center;
      flex-wrap: wrap;
      &--title {
        padding-bottom: 30px;
        font-weight: 500;
        font-size: 36px;
        line-height: 120%;
        color: #fff;
        margin: 0;
      }
    }
    .futures__inner--second {
      flex-wrap: wrap-reverse;
    }
    &__text {
      font-weight: 400;
      font-size: 24px;
      line-height: 140%;
      color: #fff;
    }
    ul.futures__text li {
      list-style-type:disc;
      font-weight: 600;
      &:not(:last-child) {
        margin-bottom: 56px;
      }
    }
    &__img {
      max-width: 100%;
    }
  }
}
@media screen and (max-width: 526px) {
  .futures {
    &__inner {
      &--title {
        font-size: 24px;
      }
    }
    &__text {
      font-size: 18px;
    }
  }
}

