.vantage {
  padding: 240px 0;
  .vantage__title {
    text-align: center;
    height: 208px;
  }
  .vantage__grid {
    display: grid;
    gap: 40px;
    grid-template-columns: repeat(3, 1fr);
    .vantage__wrapper-item {
      .vantage__item {
        display: flex;
        flex-direction: column;
        align-items: center;
        padding: 48px 40px;
        max-width: 534px;
        height: auto;
        background-color: $colorWhite;
        box-shadow: 0px 8px 40px rgba(0, 0, 0, 0.2);
        opacity: 0;
        visibility: hidden;
        .vantage__item--title {
          align-self: flex-start;
          font-family: 'Roboto', sans-serif;
          font-style: normal;
          font-weight: 500;
          font-size: 32px;
          line-height: 140%;
          color: $colorBlue;
        }
        .vantage__item--descr {
          margin-top: 24px;
          font-family: 'Roboto', sans-serif;
          font-style: normal;
          font-weight: 400;
          font-size: 24px;
          line-height: 130%;
          color: #606060;
        }
        .vantage__item--img {
          margin-top: 42px;
          width: min-content;
        }
      }
      .block-visible {
        opacity: 1;
        visibility: visible;
      }
      .vantage__item--first {
        position: relative;
        &:after {
          position: absolute;
          left: 0;
          top: 0;
          content: '';
          width: 4px;
          height: 100%;
          background-color: $colorBlue;
        }
        &::before {
          position: absolute;
          top: 0;
          left: 0;
          content: '';
          width: 108%;
          height: 4px;
          background-color: $colorBlue;
        }
      }
      .vantage__item--second {
        margin-top: 70px;
        position: relative;
        border-bottom: 4px solid $colorBlue;
        &:after {
          position: absolute;
          left: 0;
          bottom: 0;
          content: '';
          width: 4px;
          height: 116%;
          background-color: $colorBlue;
        }
        &::before {
          position: absolute;
          right: 0;
          bottom: 0;
          content: '';
          width: 4px;
          height: 85%;
          background-color: $colorBlue;
        }
      }
      .vantage__item--three {
        margin-top: 135px;
        position: relative;
        &:after {
          position: absolute;
          right: 0;
          top: 0;
          content: '';
          width: 4px;
          height: 150%;
          background-color: $colorBlue;
        }
        &::before {
          position: absolute;
          top: 0;
          right: 0;
          content: '';
          width: 108%;
          height: 4px;
          background-color: $colorBlue;
        }
      }
      .vantage__item--six {
        position: relative;
        margin-top: 135px;
        background-color: $colorBlue;
        &::before {
          position: absolute;
          top: 0;
          right: 0;
          content: '';
          width: 108%;
          height: 4px;
          background-color: $colorBlue;
        }
        .vantage__item--title {
          color: $colorWhite;
        }
        .vantage__item--descr {
          color: $colorWhite;
        }
      }
      .vantage__item--five {
        position: relative;
        margin-top: 70px;
        border-bottom: 4px solid $colorBlue;
        &:after {
          position: absolute;
          right: 0;
          bottom: 0;
          content: '';
          width: 4px;
          height: 83%;
          background-color: $colorBlue;
        }
        &::before {
          position: absolute;
          bottom: 0;
          left: 0;
          content: '';
          width: 4px;
          height: 118.8%;
          background-color: $colorBlue;
        }
      }
      .vantage__item--four {
        margin-top: -3px;
        position: relative;
        &:after {
          position: absolute;
          left: 0;
          top: 0;
          content: '';
          width: 108%;
          height: 4px;
          background-color: $colorBlue;
        }
        &::before {
          position: absolute;
          top: 0;
          left: 0;
          content: '';
          width: 4px;
          height: 141%;
          background-color: $colorBlue;
        }
      }
    }
  }
  .vantage--text {
    margin: 0 0 0 117px;
    font-family: 'Roboto', sans-serif;
    font-style: normal;
    font-weight: 400;
    font-size: 32px;
    line-height: 140%;
    color: #606060;
  }
  .small-text {
    max-width: 460px;
    position: relative;
    &:after {
      position: absolute;
      right: 0;
      bottom: -23px;
      content: '';
      width: 125.4%;
      height: 4px;
      background-color: $colorBlue;
    }
  }
  .vantage--text__big {
    margin-top: 48px;
    font-weight: 500;
    font-size: 48px;
    line-height: 120%;
    letter-spacing: 0.01em;
    color: #4048F5;
    border: none;
  }
}
@media screen and (max-width: 1440px) {
  .vantage {
    padding-top: 120px;
    padding-bottom: 120px;
    .vantage__title {
      text-align: center;
      height: 250px;
    }
    .vantage__grid {
      .vantage__wrapper-item {
        .vantage__item--first {
          &::before {
            width: 111%;
          }
        }
        .vantage__item--second {
          &:after {
            height: 110%;
          }
          &::before {
            height: 88%;
          }
        }
        .vantage__item--three {
          &::before {
            width: 111%;
          }
        }
        .vantage__item--six {
          &::before {
            width: 111%;
          }
        }
        .vantage__item--five {
          &:after {
            height: 84%;
          }
        }
        .vantage__item--four {
          &:after {
            width: 111%;
          }
          &::before {
            height: 156%;
          }
        }
      }
    }
  }
}
@media screen and (max-width: 1122px) {
  .vantage {
    .vantage__title {
      margin-top: 0;
      text-align: center;
      height: 350px;
    }
  }
}
@media screen and (max-width: 1024px) {
  .vantage {
    .vantage__title {
      text-align: center;
      margin-bottom: 150px;
    }
    .vantage__grid {
      display: flex;
      flex-wrap: wrap;
      justify-content: center;
      gap: 40px;
      .vantage__wrapper-item {
        .vantage__item {
          margin-top: 24px;
          display: flex;
          flex-direction: column;
          align-items: center;
          padding: 48px 40px;
          max-width: 534px;
          height: auto;
          background-color: $colorWhite;
          box-shadow: 0px 8px 40px rgba(0, 0, 0, 0.2);
          .vantage__item--title {
            align-self: flex-start;
            font-family: 'Roboto', sans-serif;
            font-style: normal;
            font-weight: 500;
            font-size: 32px;
            line-height: 140%;
            color: $colorBlue;
          }
          .vantage__item--descr {
            margin-top: 24px;
            font-family: 'Roboto', sans-serif;
            font-style: normal;
            font-weight: 400;
            font-size: 24px;
            line-height: 130%;
            color: #606060;
          }
          .vantage__item--img {
            margin-top: 42px;
            width: min-content;
          }
        }
        .vantage__item--first {
          position: relative;
          border-left: 4px solid $colorBlue;
          border-right: 4px solid $colorBlue;
          border-top: 4px solid $colorBlue;
          &::after {
            position: absolute;
            content: '';
            left: 100%;
            top: 100%;
            width: 4px;
            height: 65px;
          }
          &::before {
            position: relative;
            content: '';
            width: 0px;
            height: 0px;
          }
        }
        .vantage__item--second {
          position: relative;
          border-top: 4px solid $colorBlue;
          border-left: 4px solid $colorBlue;
          border-bottom: none;
          &:after {
            position: absolute;
            content: '';
            left: -4px;
            top: 100%;
            width: 4px;
            height: 65px;

          }
          &::before {
            width: 0;
            height: 0;

          }
        }
        .vantage__item--three {
          position: relative;
          border-top: 4px solid $colorBlue;
          border-right: 4px solid $colorBlue;
          &:after {
            position: absolute;
            content: '';
            right: -4px;
            top: 100%;
            width: 4px;
            height: 65px;

          }
          &::before {
            width: 0;
            height: 0;

          }
        }
        .vantage__item--six {
          position: relative;
          border-top: 4px solid $colorBlue;
          border-left: 4px solid $colorBlue;
          border-bottom: 4px solid $colorBlue;
          &:after {
            position: absolute;
            content: '';
            left: -4px;
            top: 100%;
            width: 4px;
            height: 65px;
          }
          &::before {
            width: 0;
            height: 0;

          }
          .vantage__item--title {
            color: $colorBlue;
          }
          .vantage__item--descr {
            color: #606060;
          }
        }
        .vantage__item--five {
          position: relative;
          border-top: 4px solid $colorBlue;
          border-right: 4px solid $colorBlue;
          border-bottom: none;
          &:after {
            position: absolute;
            content: '';
            right: -4px;
            top: 100%;
            width: 4px;
            height: 65px;

          }
          &::before {
            width: 0;
            height: 0;

          }
        }
        .vantage__item--four {
          position: relative;
          border-top: 4px solid $colorBlue;
          border-left: 4px solid $colorBlue;
          border-bottom: none;
          &:after {
            position: absolute;
            content: '';
            left: -4px;
            top: 100%;
            width: 4px;
            height: 65px;

          }
          &::before {
            width: 0;
            height: 0;

          }
        }
      }
    }
    .vantage--text {
      margin: 41px 0 0 117px;
      font-family: 'Roboto', sans-serif;
      font-style: normal;
      font-weight: 400;
      font-size: 32px;
      line-height: 140%;
      color: #606060;
    }
    .small-text {
      max-width: 460px;
      position: relative;
      &:after {
        position: absolute;
        left: 0;
        bottom: -23px;
        content: '';
        width: 120%;
        height: 4px;
        background-color: $colorBlue;
      }
      &::before {
        position: absolute;
        content: '';
        width: 4px;
        height: 109px;
        top: -41px;
        right: -20%;
        background-color: $colorBlue;
      }
    }
    .vantage--text__big {
      font-size: 48px;
      line-height: 120%;
      letter-spacing: 0.01em;
      color: #4048F5;
    }
  }
}
@media screen and (max-width: 768px) {
  .vantage {
    padding-top: 100px;
    padding-bottom: 50px;
    .vintage__wrapper {
      display: flex;
      flex-direction: column;
      align-items: center;
      .vantage__title {
        margin-bottom: 50px;
        font-size: 48px;
        height: auto;
      }
      .vantage--text {
        margin: 41px 0;

      }
      .small-text {
        margin: 40px 0 0 -100px;
        position: relative;
        font-size: 24px;
        &:after {
          position: absolute;
          left: 0;
          bottom: -23px;
          content: '';
          width: 130%;
          height: 4px;
          background-color: $colorBlue;
        }
        &::before {
          position: relative;
          content: '';
        }
      }
      .vantage--text__big {
        font-size: 36px;
        line-height: 120%;
        letter-spacing: 0.01em;
        color: #4048F5;
      }
    }
  }
}
@media screen and (max-width: 425px) {
  .vantage {
    padding: 50px 0;
    .vantage__title {
      text-align: center;
    }
    .vintage__wrapper {
      display: flex;
      flex-direction: column;
      align-items: center;
      .vantage__title {
        font-size: 36px;
      }
      .vantage--text {
        margin: 41px 0;

      }
      .small-text {
        margin: 40px 0 0 0;
        position: relative;
        font-size: 24px;
        &:after {
          position: absolute;
          left: 0;
          bottom: -23px;
          content: '';
          width: 120%;
          height: 4px;
          background-color: $colorBlue;
        }
        &::before {
          position: absolute;
          content: '';
          width: 4px;
          height: 109px;
