.pluses {
  padding: 120px 0;
  background-color: $colorBlack;
  .pluses__title {
    margin: 0;
    height: 312px;
    color: $colorWhite;
  }
  .pluses__inner {
      display: flex;
      align-items: center;
      flex-wrap: wrap;
      gap: 40px;
      .pluses__item {
          position: relative;
          margin-bottom: 200px;
          padding: 40px;
          z-index: 50;
          max-width: 506px;
          border: 4px solid $colorBlue;
          opacity: 0;
          visibility: hidden;
          .img__wrapper {
              margin: -120px 0 0 -40px;
              padding: 0 50px 0 20px;
              z-index: 100;
              position: absolute;
              background-color: $colorBlack;
          }
          .pluses__item--content {
            margin-top: 110px;
            .pluses__item--title {
                margin-top: 60px;
                font-family: 'Roboto', sans-serif;
                font-style: normal;
                font-weight: 500;
                font-size: 32px;
                line-height: 140%;
                color: $colorWhite;
            }
            .pluses__item--descr {
                font-family: 'Roboto', sans-serif;
                font-style: normal;
                font-weight: 400;
                font-size: 24px;
                line-height: 130%;
                color: rgba(255, 255, 255, 0.6);
            }
          }
      }
      .block-visible {
        opacity: 1;
        visibility: visible;
      }
      .pluses__item--first {
          margin-top: 300px;
      }
      .pluses__item--second {
          margin-top: 100px;
      }
      .pluses__item--three {
          margin-top: -100px;
      }
  }
}
@media screen and (max-width: 1440px) {
  .pluses {
    .pluses__inner {
        flex-wrap: nowrap;
        justify-content: center;
        gap: 30px;
        .pluses__item {
          max-width: 470px;
        }
    }
  }
}
@media screen and (max-width: 1068px) {
  .pluses {
    margin: 0 auto;
    .pluses__title {
      margin: 0;
      font-size: 56px;
    }
    .pluses__inner {
        flex-wrap: nowrap;
        justify-content: center;
        gap: 30px;
        .pluses__item {
          max-width: 470px;
          .pluses__item--content {
            .pluses__item--title {
                font-size: 24px;
            }
            .pluses__item--descr {
                font-size: 18px;
            }
          }
        }
        .pluses__item--first {
          margin: 200px 0 0 0;
        }
        .pluses__item--second {
          margin: 0px;
        }
        .pluses__item--three {
          margin: -100px 0 0 0;
        }
    }
  }
}
@media screen and (max-width: 768px) {
  .pluses {
    margin: 0 auto;
    padding: 80px 0 0 0;
    .pluses__title {
      text-align: center;
      font-size: 56px;
      margin-bottom: 75px;
    }
    .pluses__inner {
        flex-wrap: wrap;
        gap: 30px;
        .pluses__item {
          max-width: 470px;
          margin-top: 100px;
        }
        .pluses__item--three {
          margin: 100px 0;
        }
    }
  }
}
@media screen and (max-width: 425px) {
  .pluses {
    margin: 0 auto;
    .pluses__title {
      text-align: center;
      font-size: 36px;
      margin-bottom: 0;
    }
    .pluses__inner {
        flex-wrap: wrap;
        gap: 30px;
        .pluses__item {
          max-width: 470px;
          margin-top: 100px;
          .pluses__item--content {
            margin-top: 110px;
            border-top: 1px solid #999;
            .pluses__item--title {
              margin-top: 60px;
              font-size: 24px;
            }
            .pluses__item--descr {
              font-size: 18px;
            }
        }
      }
    }
  }
}

