      gap: 40px;
      margin-top: 40px;
    }
    &__title {max-width: 1270px;}
    &__item {
      width: 100%;
      position: relative;
      padding: 56px 40px 80px;
      background: #fff;
      box-shadow: 0px 8px 40px rgba(0, 0, 0, 0.2);
      &--title {
        font-weight: 600;
        font-size: 48px;
        line-height: 120%;
        color: var(--finmodal-title);
        margin-bottom: 30px;
      }
      &--descr {
        font-weight: 400;
        font-size: 24px;
        line-height: 130%;
        color: #606060;
      }
      &--first {
        top: 160px;
        border-top: 4px solid $colorBlue;
        &::after {
          position: absolute;
          content: '';
          width: 52px;
          height: 4px;
          top: -4px;
          right: -11%;
          background-color: $colorBlue;
        }
      }
      &--second {
        top: 80px;
        border-top: 4px solid $colorBlue;
        &::after {
          position: absolute;
          content: '';
          width: 50px;
          height: 4px;
          top: -4px;
          right: -11%;
          background-color: $colorBlue;
        }
        &::before {
          position: absolute;
          content: '';
          width: 4px;
          height: 84px;
          top: -4px;
          left: 0;
          background-color: $colorBlue;
        }
      }
      &--three {
        border-top: 4px solid $colorBlue;
        &::before {
          position: absolute;
          content: '';
          width: 4px;
          height: 84px;
          top: -4px;
          left: 0;
          background-color: $colorBlue;
        }
      }
    }
  }
}
@media screen and (max-width: 1024px) {
  .payback {
    &__inner {
      display: flex;
      flex-wrap: wrap;
      margin-top: 40px;
    }
    &__title {max-width: 1270px;}
    &__item {
      margin-top: 24px;
      width: 500px;
      height: auto;
      position: relative;
      padding: 56px 40px 80px;
      background: #fff;
      box-shadow: 0px 8px 40px rgba(0, 0, 0, 0.2);
      &--title {
        font-weight: 600;
        font-size: 48px;
        line-height: 120%;
        color: var(--finmodal-title);
        margin-bottom: 30px;
      }
      &--descr {
        font-weight: 400;
        font-size: 24px;
        line-height: 130%;
        color: #606060;
      }
      &--first {
        top: 0px;
        border-top: 4px solid $colorBlue;
        border-right: 4px solid $colorBlue;
        &::after {
          position: absolute;
          content: '';
          width: 4px;
          height: 65px;
          top: 100%;
          right: -4px;
          background-color: $colorBlue;
        }
      }
      &--second {
        top: 0px;
        border-top: 4px solid $colorBlue;
        border-left: 4px solid $colorBlue;
        &::after {
          position: relative;
          width: 0px;
          height: 0px;
        }
        &::before {
          position: absolute;
          content: '';
          width: 4px;
          height: 84px;
          top: 100%;
          left: -4px;
          background-color: $colorBlue;
        }
      }
      &--three {
        border-top: 4px solid $colorBlue;
        border-right: 4px solid $colorBlue;
        &::before {
          position: relative;
          width: 0px;
          height: 0px;
        }
      }
    }
    .table__wrapper {
      overflow-x: auto;
      width: 110%;
      visibility: visible;
    }
  }
}
@media screen and (max-width: 768px) {
  .payback {
    .payback__title {
      font-size: 56px;
    }
  }
}
@media screen and (max-width: 512px) {
  .payback {
    .payback__title {
      font-size: 36px;
    }
    &__item {
      &--title {
        font-size: 36px;
      }
    }
  }
}
@media screen and (max-width: 425px) {
  .payback {
    padding: 50px 0;
    .payback__title {
      font-size: 24px;
    }
    &__item {
      &--title {
        font-size: 24px;
      }
    }
  }
}