.project {
  padding-bottom: 140px;
  background-color: #fff;
  &__title {
    margin-bottom: 40px;
    opacity: 0;
    visibility: hidden;
  }
  .project__descr {
    opacity: 0;
    visibility: hidden;
  }
  &__inners {
    display: flex;
    flex-direction: column;
    align-items: center;
  }
  &__inner {
    margin-top: var(--container-offset);
    display: flex;
    gap: 200px;
  }
  &__item {
    opacity: 0;
    visibility: hidden;
    &--content {
      height: 276px;
      max-width: 680px;
      padding: 36px;
      background: #fff;
      box-shadow: 0px 8px 40px rgba(0, 0, 0, 0.2);
      display: flex;
      align-items: flex-end;
      justify-content: start;
      position: relative;
      margin-bottom: 24px;
    }
    &--img {
      position: absolute;
      bottom: 0;
      right: -170px;
    }
    &--title {
      max-width: 422px;
      font-weight: 600;
      font-size: 48px;
      line-height: 120%;
      letter-spacing: 0.01em;
      color: var(--finmodal-title);
    }
    &--subtitle {
      font-weight: 400;
      font-size: 24px;
      line-height: 130%;
      color: var( --menu-color);
      max-width: 721px;
      margin-left: 121px;
    }
    &--second{
      margin-top: 80px;
    }
    .project__item--img {
      bottom: 16px;
      right: -140px;
    }
    &--three {
      .project__item--img  {
        right: -125px;
      }
    }
    &--four {
      margin-top: 80px;
      .project__item--img {
        bottom: 16px;
        right: -83px;
      }
    }
  }
  .block-visible {
    opacity: 1;
    visibility: visible;
  }
}
.block-visible {
  opacity: 1;
  visibility: visible;
}
@media screen and (max-width: 1024px) {
  .project {
    padding-bottom: 240px;
    background-color: #fff;
    &__title {
      margin-bottom: 40px;
    }
    &__inners {
      display: flex;
      flex-direction: column;
      align-items: center;
    }
    &__inner {
      margin-top: var(--container-offset);
      display: flex;
      flex-wrap: wrap;
      gap: 50px;
    }
  }
}
@media screen and (max-width: 768px) {
  .project {
    padding: 0 10px 100px 10px;
    background-color: #fff;

    &__inner {
      margin-top: var(--container-offset);
      display: flex;
      flex-wrap: wrap;
      gap: 50px;
    }
    &__item {
      width: auto;
      &--content {
        width: auto;
        position: relative;
        z-index: 50;
        position: relative;
        margin-bottom: 24px;
      }
      &--img {
        z-index: 0;
        position: absolute;
        top: 0;
        right: 0;
      }
      &--subtitle {
        margin: 0;
      }
    }
  }
}
@media screen and (max-width: 425px) {
  .project {
    &__title {
      margin-bottom: 40px;
      font-size: 36px;
    }
    .project__descr {
      font-size: 24px;
    }
    &__inner {
      margin-top: var(--container-offset);
      display: flex;
      flex-wrap: wrap;
      gap: 50px;
    }
    &__item {
      width: auto;
      &--content {
        width: auto;
        position: relative;
        z-index: 50;
        position: relative;
        margin-bottom: 24px;
      }
      &--title {
        max-width: 422px;
        font-weight: 600;
        font-size: 36px;
        line-height: 120%;
        letter-spacing: 0.01em;
        color: var(--finmodal-title);
      }
      &--subtitle {
        font-weight: 400;
        font-size: 18px;
        line-height: 130%;
        color: var( --menu-color);
        max-width: 721px;
        margin-left: 121px;
      }
      &--img {
        height: min-content;
        z-index: 0;
        position: absolute;
        top: 0;
        right: 0;
      }
      &--subtitle {
        margin: 0;
      }
    }
  }
}

