.about {
  height: auto;
  padding: 0 0 150px;
  position: relative;
  &__inner {
    height: 975px;
  }
  &__text {
    opacity: 0;
    visibility: hidden;
    position: absolute;
    font-weight: 400;
    font-size: 32px;
    line-height: 140%;
    color: var(--menu-color);
    &--one {
      max-width: 634px;
      top: 50px;
      left:855px;
    }
    &--two {
      max-width: 685px;
      left: var( --container-offset);
      bottom: -60px;
    }
  }
  &__block {
    opacity: 0;
    visibility: hidden;
    position: absolute;
    background: #fff;
    box-shadow: 0px 8px 40px rgba(0, 0, 0, 0.05);
    transform: rotate(45.13deg);
    padding: 24px;
    div {
      width: 100%;
      height: 100%;
      display: flex;
      flex-direction: column;
      align-items: center;
      justify-content: center;
    }
    &--title {
      font-weight: 600;
      font-size: 80px;
      line-height: 130%;
     margin-left: -140px;
      color: var(--finmodal-title);
      transform: rotate(-45.13deg);
      &__one {
        margin-left: -60px;
      }
    }
    &--subtitle {
      margin-left: 10px;
      font-weight: 600;
      font-size: 48px;
      color: var( --title-color);
      transform: rotate(-45.13deg);
    }
    &--text {
      font-weight: 400;
      font-size: 24px;
      text-align: center;
      color: var(--title-color);
      transform: rotate(-45.13deg);
      margin-left: 155px;
      margin-top: 0;
      &__one {
        margin-left: 60px;
        margin-top: -30px;
      }
    }
    &--one {
      width: 329.31px;
      height: 329.31px;
      left: var( --container-offset);
      top: 40px;
    }
    &--two {
      width: 458.35px;
      height: 458.35px;
      left: 458.35px;
      top: calc(325px - 106px);
    }
    &--fhree {
      width: 412.58px;
      height: 412.58px;
      left:995.38px;
      top: 485px;
    }
    &--four {
      width: 392.11px;
      height: 392.11px;
      left: 1406px;
      top: 210px;
    }
  }
  .block-visible {
    opacity: 1;
    visibility: visible;
  }
  
}
@media screen and (max-width: 1920px) {
  .about {
    &__text {
      &--one {
        max-width: 634px;
        top: 20px;
        left: 600px;
      }
    }
    &__block {
      &--one {
        width: 300px;
        height: 300px;
        left: var( --container-offset);
        top: 40px;
      }
      &--two {
        width: 420px;
        height: 420px;
        left: 350px;
        top: calc(325px - 106px);
      }
      &--fhree {
        width: 380px;
        height: 380px;
        left: 690px;
        top: 520px;
      }
      &--four {
        width: 400px;
        height: 400px;
        left: 1030px;
        top: 260px;
      }
    }
  }
}
