// если вы хотите использовать sass-переменные - удалите root
// colors
:root {
  // base
  --font-family: "Roboto", sans-serif;
  --content-width: 1680px;
  --container-offset: 110px;
  --container-width: calc(var(--content-width) + (var(--container-offset) * 2));

  // background colors
  --light-color: #fff;
  --main-color: #f6f6f6;

  // Fonts Colors
  --menu-color: #606060;
  --menu-color__hover:#4048f5;
  --title-color: #262626;
  --finmodal-title: #4048f5;
  //  FONTS SIZE
  --fz-24: 24px;
  --fz-32: 32px;
}
