.finmodel{
  height: 1213px;
  position: relative;
  padding-top: 5px;
  &__title {
    position: relative;
    z-index: 20;
    margin-bottom: 51px;
    height: 234px;
  }
  &__inner {
    z-index: 20;
    position: relative;
    display: flex;
    flex-direction: column;
    .piroliz__svg {
      z-index: 1;
      position: absolute;
      width: 63%;
      right: -28px;
      top: 30px;
      transition: 0.7s ease-in;
    }
    .piroliz__png {
      z-index: 1;
      position: absolute;
      width: 63%;
      right: -28px;
      top: 30px;
      transition: 1s ease-in;
    }
    .hide {
      opacity: 0;
      visibility: hidden;
    }
  }
  &__list {
    z-index: 20;
    margin: 0 0 0 120px;
    display: flex;
    align-items: center;
    gap: 50px;
  }
  &__item {
    z-index: 20;
    position: relative;
    &--title {
      font-weight: 500;
      font-size: 32px;
      line-height: 140%;
      color: var(--finmodal-title);
      &--subtitle {
        font-weight: 400;
        font-size: 24px;
        line-height: 130%;
        color: var(--title-color);
      }
    }
    &:not(:last-child)::before {
      content: '';
      display: block;
      position: absolute;
      right: -30px;
      width: 1px;
      height: 100%;
      background-color: var(--finmodal-title);
      transform: rotate(20deg);
    }
  }
  &__svg {
    z-index: 20;
    position: absolute;
    &--top {
      width: 218px;
      height: 395px;
      top: -220px;
      right: -50px;
    }
    &--bottom {
      width: 515px;
      height: 381px;
      bottom: -320px;
      left: 0;
    }
    &--adaptiv {
      display: none;
    }
    
  }
  
}
@media screen and (max-width: 1842px) {
  .finmodel {
    &__title {
      height: 300px;
    }
  }
}
