.pdffuel {
  position: relative;
  display: flex;
  .pdffuel__block {
    .pdffuel__content {
      position: relative;
      min-height: 1074px;
      padding: 120px 0;
      display: flex;
      flex-direction: column;
      justify-content: space-between;
      .block__img {
        position: absolute;
        top: 30%;
        z-index: 10;
      }
      .block__text {
        position: relative;
        z-index: 100;
        font-family: 'Roboto', sans-serif;
        font-style: normal;
        font-weight: 400;
        font-size: 24px;
        line-height: 130%;
        color: #999999;
      }
    }
    &--left {
      background-color: $colorBlack;
    }
    &--right {
      background-color: $colorBlue;
    }
  }
  .pdffuel__image {
    position: absolute;
    top: 200px;
    left: 17%;
    z-index: 20;
  }
}
@media screen and (max-width: 1024px) {
  .pdffuel { 
    .pdffuel__image {
      position: absolute;
      top: 200px;
      left: 0%;
      z-index: 20;
    }
  }
}
@media screen and (max-width: 900px) {
  .pdffuel {
    position: relative;
    display: flex;
    flex-direction: column;
    .pdffuel__block {
      .pdffuel__content {
        position: relative;
        min-height: 500px;
        padding: 80px 0;
        display: flex;
        flex-direction: column;
        justify-content: start;
        .block__img {
          position: relative;
          z-index: 10;
        }
        .block__text {
          margin: 10px 0;
          position: relative;
          z-index: 100;
        }
        &--bottom {
          flex-direction: column-reverse;
        }
      }
    }
    .pdffuel__image {
      position: absolute;
      top: 25%;
      left: 5%;
      z-index: 20;
    }
  }
}
@media screen and (max-width: 900px) {
  .pdffuel {
    .pdffuel__block {
      .pdffuel__content {
        .block__text {
          margin: 10px 0;
          font-size: 16px;
        }
      }
    }
  }
}
@media screen and (max-width: 425px) {
  .pdffuel {
    .pdffuel__block {
      .pdffuel__content {
        .block__text {
          margin: 10px 0;
          font-size: 16px;
        }
      }
    }
    .pdffuel__image {
      position: absolute;
      top: 35%;
      left: 5%;
      z-index: 20;
    }
  }
}