.programs {
  padding: 120px 0 200px;
  background-color: #f6f6f6;
  &__title {
    margin-bottom: 80px;
    height: 350px;
  }
  &__inner {
    display: flex;
    flex-wrap: wrap;
    gap: 40px;
    position: relative;
  }
  &__item {
    max-width: 820px;
    padding: 48px 70px 80px;
    background: #fff;
    box-shadow: 0px 8px 40px rgba(0, 0, 0, 0.2);
    &--title {
      font-weight: 600;
      font-size: 32px;
      line-height: 140%;
      color: #4048f5;
      margin-bottom: 104px;
    }
    &--list {
      border-top:  1px solid #999;
      padding-top: 56px;
      margin-bottom: 56px;
      li {
        margin-bottom: 30px;
        font-weight: 400;
        font-size: 24px;
        line-height: 130%;
        color: #606060;
        list-style-type: disc;
      }
    }
    &--first {
      max-height: 420px;
    }
  }
  .finmodel__form {
    padding-bottom: 0;
  }
  .finmodel__form--input {
    width: 437px;
    height: 72px;
  }
  svg {
    position: absolute;
    bottom: -90px;
    left: -194px;
  }
}
