.own {
  padding: 240px 0 120px;
  background-color: #f6f6f6;
  &__title {
    font-size: 56px;
    margin-bottom: 80px;
  }
  &__inner {
    &--first {
      grid-template-columns: 1fr;
      grid-template-rows: 284px 315px 268px;
      gap: 80px;
    }
    &--second {
      margin-top: 80px;
      grid-template-columns: repeat(3, 533px);
      grid-template-rows: 467px;
      gap: 40px;

    }
  }
  &__first {
    &--item {
      background: #FFFFFF;
      box-shadow: 0px 8px 40px rgba(0, 0, 0, 0.2);
      position: relative;
      padding: 80px;
      display: flex;
      align-items: center;
      justify-content: center;

      svg {
        position: absolute;
      }
      &__first {
        max-width: 1320px;
        left: 359px;
        justify-content: end;
        padding-right: 120px;
        .own__first--item__text {
          max-width: 609px;
        }
        svg {
          left: -360px;
          width: 704.28px;
          height: 168.21px;
        }
      }
      &__second {
        max-width: 1125px;
        left: 0;
        justify-content: start;
        padding-right: 80px;
        svg {
          right: -129px;
        }
      }
      &__three {
        max-width: 1175px;
        left: 406px;
        justify-content: end;
        padding-right: 120px;
        svg {
          left: -128px;
        }
      }
      &__text {
        max-width: 700px;
      }
    }
    .block-visible {
    opacity: 1;
    visibility: visible;
    }
  }
  &__second {
    &--item {
      background: #4048F5;
      box-shadow: 0px 8px 40px rgba(0, 0, 0, 0.2);
      padding: 56px 40px;
      &__title {
        font-weight: 600;
        font-size: 32px;
        line-height: 140%;
        color: #f6f6f6;
        margin-bottom: 30px;
      }
      &__text {
        font-weight: 400;
        font-size: 24px;
        line-height: 130%;
        color: #f6f6f6;
      }
    }
  }
}
