.videoblock {
    background-color: #fff;
    .videoblock__wrapper {
        display: flex;
        justify-content: center;
        align-items: center;
        .video {
            width: 100%;
            height: auto;
        }
        .video_desctop {
            display: block;
        }
        .video_mobile {
            display: none;
        }
    }
    
}

@media screen and (max-width: 768px) {
    .videoblock {
        .videoblock__wrapper {
            .video_desctop {
                display: none;
            }
            .video_mobile {
                display: block;
            }
        }
    }
}