.products {
  padding: 168px 0 320px;
  background-color: #fff;
  &__title {
    margin-bottom: 40px;
    opacity: 0;
    visibility: hidden;
  }
  &__descr {
    margin-bottom: 168px;
  }
  &__grid {
    justify-content: center;
    grid-template-columns: repeat(3, 400px);
    grid-template-rows:repeat(2, 223px);
    gap: 40px;
  }
  &__item {
    padding: 56px 0 56px 40px;
    background-color: var(--finmodal-title);
    box-shadow: 0px 8px 40px rgba(0, 0, 0, 0.2);
    position: relative;
    opacity: 0;
    visibility: hidden;
    &--title {
      font-weight: 600;
      font-size: 48px;
      line-height: 120%;
      color: #fff;
      margin-bottom: 8px;
    }
    &--descr {
      font-weight: 400;
      font-size: 32px;
      line-height: 140%;
      color: #fff;
    }

    &--five {
      grid-column: 2/4;
      background-color: #fff;
      box-shadow: none;
      .products__item--descr {
        max-width: 70%;
        font-weight: 600;
        color: var(--finmodal-title);
      }
    }
    &--img {
      position: absolute;
      z-index: 10;
    }
    &--first {
      .products__item--img {
        right: -30px;
        top:-120px;
      }
    }
    &--second {
      .products__item--img {
        right: 30px;
        top:-120px;
      }
    }
    &--three {
      .products__item--img {
        right: 30px;
        top:-110px;
      }
    }
    &--four {
      .products__item--img {
        right: 15px;
        bottom:-78px;
      }
    }
  }
  .block-visible {
    opacity: 1;
    visibility: visible;
  }
}
@media screen and (max-width: 1024px) {
  .products {
    &__grid {
      grid-template-columns: repeat(2, 250px);
      grid-template-rows: repeat(3, 280px);
      gap: 30px;
    }
  }
}
@media screen and (max-width: 768px) {
  .products {
    padding: 70px 0 120px;
    &__title {
      font-size: 56px;
    }
    &__grid {
      display: flex;
      flex-wrap: wrap;
      gap: 50px;
    }
    &__descr {
      margin-bottom: 68px;
    }
    &__item {
      margin-top: 90px;
      width: 335px;
      height: 196px;  
      &--five {
        .products__item--descr {
          max-width: 100%;
          font-weight: 600;
          color: var(--finmodal-title);
        }
      }
    }
  }
}
@media screen and (max-width: 425px) {
  .products {
    &__title {
      font-size: 36px;
    }
    &__descr {
      margin-bottom: 50px;
    }
  }
}
