.apperance {
  padding: 120px 0 50px;
  background-color: #fff;
  position: relative;
  &__title {
    max-width: 740px;
    margin-bottom: 90px;
    opacity: 0;
    visibility: hidden;
  }
  &__inner {
    position: relative;
    background-image: url(../img/svg/apperance/bg.svg);
    background-repeat: no-repeat;
    background-size: contain;
    background-position: center;
    height: 800px;
  }
  &__img {
    width: min-content;
    opacity: 0;
    visibility: hidden;
    &--first {
      position: absolute;
      left: 12%;
      top: -5%;
    }
    &--second {
      position: absolute;
      right: 15%;
      top: -25%;
    }
  }
  .block-visible {
    opacity: 1;
    visibility: visible;
  }
}
@media screen and (max-width: 1440px) {
  .apperance {
    &__img {
      width: min-content;
      &--first {
        position: absolute;
        left: 7%;
        top: -5%;
      }
      &--second {
        position: absolute;
        right: 0%;
        top: -27%;
      }
    }
  }
}
@media screen and (max-width: 1024px) {
  .apperance {
    &__title {
      max-width: 740px;
      margin-bottom: 90px;
      text-align: center;
    }
    &__img {
      width: 400px;
      height: auto;
      &--first {
        position: absolute;
        left: 7%;
        top: -5%;
      }
      &--second {
        position: absolute;
        right: 0%;
        top: 60%;
      }
    }
  }
}
@media screen and (max-width: 768px) {
  .apperance {
    padding: 50px 0;
    &__title {
      font-size: 56px;
      text-align: center;
    }
    &__inner {
      height: auto;
      display: flex;
      flex-direction: column;
      flex-wrap: wrap;
    }
    &__img {
      width: 400px;
      height: auto;
      &--first {
        margin-bottom: 142px;
        position: relative;
        align-self: flex-start;
      }
      &--second {
        position: relative;
        align-self: flex-end;
      }
    }
  }
}
@media screen and (max-width: 425px) {
  .apperance {
    &__title {
      font-size: 36px;
    }
    &__img {
      width: 320px;
      height: auto;
      &--first {
        margin-bottom: 142px;
        position: relative;
        align-self: flex-start;
      }
      &--second {
        position: relative;
        align-self: flex-end;
      }
    }
  }
}