.recast {
  position: relative;
  z-index: 50;
  padding: 150px 0;
  background-color: #fff;
  background-size: 74%;
  background-repeat: no-repeat;
  background-position: center left 60px;
  &__grid {
    z-index: 50;
    position: relative;
    gap: 40px;
    grid-template-columns: repeat(3, 532px);
    grid-template-rows: 402px 540px;
    
  }
  &__item {
    z-index: 50;
    padding: 64px 40px;
    background: #fff;
    box-shadow: 0px 8px 40px rgba(0, 0, 0, 0.2);
    position: relative;
    opacity: 0;
    visibility: hidden;
    &--first {
      grid-row: 1/3;
    }
    &--second {
      grid-column: 2/4;
      grid-row: 1/2;
    }
    &--three {
      grid-column: 2/3;
    }
    &--four {
      grid-column: 3/4;
    }
    &--title {
      font-weight: 600;
      font-size: 48px;
      line-height: 120%;
      color: var(--title-color);
    }
    &--descr {
      font-weight: 400;
      font-size: 24px;
      line-height: 130%;
      margin-top: 20px;
    }
    &--colored {
      background-color: var(--finmodal-title);
      .recast__item--descr, .recast__item--title {
        color: #fff;
      }

    }
    &--img{
      display: block;
      position:absolute;
      bottom:64px;
      left: 50%;
      transform: translateX(-50%);
    }
  }
  .recast__bg {
    opacity: 0;
    visibility: hidden;
    position: absolute;
    width: 80%;
    left: 2%;
    top: 20%;
    z-index: 0;
  }
  .block-visible {
    opacity: 1;
    visibility: visible;
  }
}
@media screen and (max-width: 1440px) {
  .recast {
    &__grid {
      gap: 40px;
      grid-template-columns: repeat(3, 400px);
    }
    &__item {
      padding: 50px 30px;
    }
  }
}
@media screen and (max-width: 1024px) {
  .recast {
    &__grid {
      gap: 10px;
      grid-template-columns: repeat(3, 250px);
    }
    &__item {
      padding: 50px 30px;
    }
  }
}
@media screen and (max-width: 768px) {
  .recast {
    padding: 50px 0 20px 0;
    &__grid {
      display: flex;
      flex-wrap: wrap;
      flex-direction: column;
    }
    &__item {
      padding: 50px 30px;
      display: flex;
      flex-wrap: wrap;
      gap: 20px;
      justify-content: center;
      align-items: center;
      &--img{
        display: block;
        position: relative;
        bottom: 0;
        left: 50%;
        transform: translateX(-50%);
      }
    }
    .recast__item--first {

      .recast__item--img {
        left: 50%;
      }
    }
    .recast__bg {
      display: none;
    }
  }
}
