html {
  box-sizing: border-box;
}

*,
*::before,
*::after {
  box-sizing: inherit;
  margin: 0;
  padding: 0;
}
ul, li {
  list-style-type: none;
}
fieldset {
  border: none;
}

.page {
  height: 100%;
  font-family: var(--font-family, sans-serif);
  font-weight: 400;
  -webkit-text-size-adjust: 100%;
}

.page__body {
  margin: 0;
  min-width: 320px;
  min-height: 100%;
  font-size: var(--fz-24);
}

img {
  height: auto;
  max-width: 100%;
  object-fit: cover;
}

a {
  text-decoration: none;
}

.site-container {
  overflow: hidden; // если используете на сайте position: sticky - уберите эту настройку
}



.input-reset {
  -webkit-appearance: none;
  appearance: none;
  border: none;
  border-radius: 0;
  background-color: #fff;

  &::-webkit-search-decoration,
  &::-webkit-search-cancel-button,
  &::-webkit-search-results-button,
  &::-webkit-search-results-decoration {
    display: none;
  }
}

.container {
  position: relative;
  margin: 0 auto;
  padding: 0 var(--container-offset);
  max-width: var(--container-width);
  @media screen and (max-width: 768px) {
    padding: 0 20px;
  }
  @media screen and (max-width: 375px) {
    padding: 0 5px;
  }
}


.centered {
  text-align: center;
}

.title {
  font-family: 'Roboto', sans-serif;
  font-weight: 600;
  font-size: 80px;
  line-height: 130%;
  letter-spacing: 0.01rem;
  color: var(--title-color);
}

.descr {
    margin: 0 auto;
    max-width: 680px;
    font-weight: 400;
    font-size: 24px;
    line-height: 130%;
    text-align: center;
    color: #606060;
}
.grid {
  display: grid;
}

$colorGrey: #606060;
$colorBlue: #4048F5;
$colorBlack: #262626;
$colorWhite: #FFFFFF;
