.finmodel__form {
  padding-bottom: 200px;
  &--title {
    font-weight: 600;
    font-size: 48px;
    margin-bottom: 10px;
    .colored {
      color: var(--finmodal-title);
    }
  }
  .inputs {
    display: flex;
    align-items: center;
    justify-content: center;
    gap: 16px;
  }
  &--input {
    width: 588px;
    height: 72px;
    padding: 22px;
    background-color: #fff;
    border: 1px solid #999999;
    transition: border-color .4s ease-in, outline .4s ease-in ;
    outline: none;
    font-weight: 400;
    font-size: 24px;
    line-height: 28px;
    &::placeholder {
      color: #999;
    }
    &:focus {
      border-color: var(--finmodal-title);
      outline: var(--finmodal-title);
    }
  }
  &--btn {
  padding: 22px 63px;
  width: 267px;
  height: 72px;
  background-color: var(--finmodal-title);
  font-weight: 400;
  font-size: 24px;
  line-height: 28px;
  text-transform: uppercase;
  color: #fff;
  cursor: pointer;
  outline: none;
  &:focus {
    background-color: #0a108d;
  }
  }
}
@media screen and (max-width: 1024px) {
  .finmodel__form {
    padding-bottom: 240px;
    &--title {
      font-weight: 600;
      font-size: 36px;
      margin-bottom: 10px;
      .colored {
        color: var(--finmodal-title);
      }
    }
    .inputs {
      display: flex;
      align-items: center;
      justify-content: center;
      gap: 16px;
    }
    &--input {
      width: auto;
      height: auto;
    }
  }
}
@media screen and (max-width: 768px) {
  .finmodel__form {
    padding-bottom: 150px;
    .inputs {
      display: flex;
      flex-wrap: wrap;
      align-items: center;
      justify-content: center;
      gap: 16px;
    }
    &--input {
      width: auto;
      height: auto;
    }
  }
}
@media screen and (max-width: 425px) {
  .finmodel__form {
    margin-top: 50px;
    &--title {
      font-size: 24px;
      margin-bottom: 10px;
    }
    .inputs {
      display: flex;
      flex-wrap: wrap;
      align-items: center;
      justify-content: center;
      gap: 16px;
    }
    &--input {
      width: 250px;
      height: 50px;
    }
    &--btn {
      width: auto;
      height: 50px;
      font-size: 18px;
      display: flex;
      justify-content: center;
      align-items: center;
    }
  }
}
@media screen and (max-width: 368px) {
  .finmodel__form {
    &--title {
      font-weight: 600;
      font-size: 24px;
      margin-bottom: 10px;
    }
    .inputs {
      display: flex;
      flex-wrap: wrap;
      align-items: center;
      justify-content: center;
      gap: 10px;
    }
    &--input {
      padding: 5px;
      width: 250px;
      height: 50px;
      font-size: 14px;
    }
    &--btn {
      width: auto;
      height: 50px;
      font-size: 18px;
      display: flex;
      justify-content: center;
      align-items: center;
    }
  }
}
