.header {
  max-height: 135px;
  background-color: #fff;
  padding: 30px 0 25px 0;
  &__inner {
    display: flex;
    align-items: center;
    justify-content: space-between;
  }

}
.logo {
  width: 133px;
  height: 55px;
  background: linear-gradient(180deg, #4048F5 0%, #191C61 100%);
  border-radius: 43px;
}

// MENU
.menu {
  &__list {
    display: flex;
    align-items: center;
    gap: 72px;
    list-style-type: none;
  }
  &__link {
    font-family: 'Roboto';
    font-weight: 400;
    font-size: 24px;
    line-height: 130%;
    color: var(--menu-color);
    transition: color .4s ease-in-out;
    &:hover {
      color: var(--menu-color__hover);
    }
  }
}
.menu__burger {
  display: none;
}

@media screen and (max-width: 1024px) {
  .menu {
    margin: 0 0 0 50px;
    &__list {
      display: flex;
      justify-content: space-between;
      align-items: center;
      list-style-type: none;
    }
    &__link {
      font-family: 'Roboto';
      font-weight: 400;
      font-size: 24px;
      line-height: 130%;
      color: var(--menu-color);
      transition: color .4s ease-in-out;
      &:hover {
        color: var(--menu-color__hover);
      }
    }
  }
}
@media screen and (max-width: 768px) {
  .header {
    position: relative;
    height: auto;
    background-color: #fff;
    padding: 40px 0 45px;
    z-index: 50;
    &__inner {
      display: flex;
      justify-content: space-between;
      .menu {
        opacity: 0;
        visibility: visible;
        padding: 0 20px;
        position: absolute;
        right: -20px;
        top: 0;
        width: 50vw;
        height: 40vh;
        padding: 35px;
        background-color: $colorWhite;
        transition: 0.2s ease-in-out;
        &__list {
          position: relative;
          z-index: 100;
          width: auto;
          height: auto; 
          display: flex;
          flex-direction: column;
          align-items: flex-start;
          gap: 30px;
        }
      }
      .menu__active {
        opacity: 1;
        visibility: visible;
      }
      .menu__burger {
        transition: 0.3s;
        display: block;
        position: relative;
        z-index: 150;
        top: -10px;
        right: 15px;
        .burger__line {
          width: 35px;
          height: 5px;
          background-color: $colorBlack;
          margin: 7px 0;
        }
      }
      .active__burger {
        .one {
          transition: 0.3s;
          position: absolute;
          transform: rotate(45deg);
        }
        .two {
          transition: 0.3s;
          position: absolute;
          transform: rotate(-45deg);
          top: -0px;
        }
        .three {
          display: none;
        }
      }
    }
  
  }
  
}
@media screen and (max-width: 425px) {
  .header {
    max-height: 135px;
    background-color: #fff;
    padding: 20px;
    &__inner {
      justify-content: space-between;
    }  
  }
  .logo {
    width: 77.38px;
    height: 32px;
    background: linear-gradient(180deg, #4048F5 0%, #191C61 100%);
    border-radius: 43px;
  }
}
