.footer {
  background-color: var(--main-color);
  padding: 120px 0;
  .footer__inner {
    display: grid;
    grid-template-columns: 1fr 2fr;
    .footer__block--menu {
      margin-bottom: 40px;
      padding-bottom: 40px;
      border-bottom: 1px solid #999;
    }
  }
}
.policy {
  font-weight: 400;
  font-size: 24px;
  line-height: 130%;
  text-decoration-line: underline;
  color: #999;
  transition: color .4s ease-in-out;
  &:hover {
    color: var(--menu-color__hover);
  }
}
@media screen and (max-width: 1024px) {
  .footer {
    .footer__inner {
      display: flex;
      flex-direction: column;
      flex-wrap: wrap;
      align-items: flex-start;
      justify-content: start;
      .footer__block--menu {
        margin: 64px 0;
        border-bottom: 1px solid #999;
        .footer__block--menu__list {
          display: flex;
          flex-direction: column;
          align-items: flex-start;
          justify-content: start;
        }
      }
      .inner {
        width: auto;
      }
    }
  }
}
@media screen and (max-width: 768px) {
  .footer {
    background-color: var(--main-color);
    padding: 120px 0;
    &__inner {
      display: grid;
      grid-template-columns: 1fr 2fr;
      .menu {
        margin-bottom: 40px;
        padding-bottom: 40px;
        border-bottom: 1px solid #999;
      }
      .inner {
        width: auto;
      }
    }
  }
  .policy {
    font-weight: 400;
    font-size: 24px;
    line-height: 130%;
    text-decoration-line: underline;
    color: #999;
    transition: color .4s ease-in-out;
    &:hover {
      color: var(--menu-color__hover);
    }
  }
}