.pluses-down {
  height: 947px;
  padding: 0 0 240px 0;
  background-color: #262626;
  &__wrapper {
    display: flex;
    align-items: center;
    gap: 56px;
    .pluses-down__img {
      width: 100%;
    }
    .pluses-down__descr {
      margin-top: -20px;
    }
    .pluses-down__text {
      max-width: 730px;
      font-weight: 400;
      font-size: 32px;
      line-height: 140%;
      color: #fff;
      margin-bottom: 40px;
    }
  }
}
@media screen and (max-width: 1440px) {
  .pluses-down {
    height: 970px;
    position: relative;
    padding: 0 0 0 0;
    background-color: #262626;
    .pluses-down__wrapper {
      display: flex;
      flex-wrap: wrap-reverse;
      align-items: center;
      gap: 56px;
      .pluses-down__img {
        position: absolute;
        width: min-content;
        right: 0;
        top: 90%;
      }
      .pluses-down__descr {
        margin-top: -20px;
        
      }
      .pluses-down__text {
        max-width: 730px;
        font-weight: 400;
        font-size: 32px;
        line-height: 140%;
        color: #fff;
        margin-bottom: 40px;
      }
    }
  }
}
@media screen and (max-width: 768px) {
  .pluses-down {
    height: 700px;
    padding: 0;
    .pluses-down__wrapper {
      .pluses-down__text {
        max-width: 730px;
        font-weight: 400;
        font-size: 24px;
        line-height: 140%;
        color: #fff;
        margin-bottom: 40px;
      }
    }
  }
}
@media screen and (max-width: 425px) {
  .pluses-down {
    height: 800px;
    padding: 0;
    .pluses-down__wrapper {
      .pluses-down__text {
        max-width: 730px;
        font-weight: 400;
        font-size: 24px;
        line-height: 140%;
        color: #fff;
        margin-bottom: 40px;
      }
    }
  }
}
